import React from 'react';
import Navigation from '../components/Navigation';
import { CopyToClipboard } from 'react-copy-to-clipboard';

const contact = () => {
    return (
        <div className="contact">
            <Navigation />
            <div className="contactContent">
                <div className="header"></div>
                <div className="contactBox">
                    <h1>Contactez-moi</h1>
                    <ul>
                        <li>
                            <i className="fas fa-map-marker-alt"></i>
                            <span> 15 Rue des Fossettes, 95330, Domont</span>
                        </li>
                        <li >
                            <i className="fas fa-mobile-alt"></i>
                        <CopyToClipboard text="0656692764">
                            <span 
                            className="clickInput" 
                            onClick={()=>{alert("Téléphone copié !")}}> 0656692764</span>
                        </CopyToClipboard>
                            </li>
                        <li>
                            <i className="far fa-envelope"></i>
                        <CopyToClipboard text="jlassi.mohamed.zied@gmail.com">
                            <span 
                            className="clickInput" 
                            onClick={()=>{alert("E-mail copié !")}}>jlassi.mohamed.zied@gmail.com</span>
                        </CopyToClipboard>
                            </li>
                    </ul>
                </div>
                <div className="socialNetwork">
                    <ul>            
                        <a href="https://www.linkedin.com/in/mohamed-zied-jlassi-65143bba/" target="_blank" rel="noopenber noreferrer">
                            <h4>Linkedin</h4>
                            <i className="fab fa-linkedin"></i>
                        </a>

                        <a href="https://github.com/Jlassi-mohamed-Zied" target="_blank" rel="noopenber noreferrer">
                            <h4>Github</h4>
                            <i className="fab fa-github"></i>
                            </a>
         
                        <a href="https://twitter.com/jlassimedzied" target="_blank" rel="noopenber noreferrer">
                            <h4>Twitter</h4>
                            <i className="fab fa-twitter"></i>
                            </a>           
                    </ul>
                </div>
            </div>
        </div>
    );
};

export default contact;