import React from 'react';

const Hobbies = () => {
    return (
        <div className="hobbies">
            <h3>Interêts</h3>
            <ul>
            <li className="hobby"><i class="fab fa-forumbee"><span>Apiculture</span></i></li>
            <li className="hobby"><i class="fas fa-futbol"><span>Football</span></i></li>
            <li className="hobby"><i class="fas fa-yin-yang"><span>Taekwondo</span></i></li>
            </ul>
        </div>
    );
};

export default Hobbies;