import React from 'react';
import { NavLink } from 'react-router-dom';

const Navigation = () => {
    return (
        <div className="sidebar">
            <div className="id">
                <div className="idcontent">
                    <img src="./media/zied.jpg" alt="profil-pic" />
                    <h3>JLASSI MOHAMED ZIED</h3>
                </div>
            </div>

            <div className="navigation">
                <ul>
                    <li>
                    <NavLink exact to="/" activeClassName="navActive">
                        <i className="fas fa-home"></i>
                        <span>Accueil</span>
                    </NavLink>
                    </li>
                    <li>
                    <NavLink exact to="/competences" activeClassName="navActive">
                        <i className="fas fa-tachometer-alt"></i>
                        <span>Compétences</span>
                    </NavLink>
                    </li>
                    <li>
                    <NavLink exact to="/portfolio" activeClassName="navActive">
                        <i className="fas fa-book-open"></i>
                        <span>Portfolio</span>
                    </NavLink>
                    </li>
                    <li>
                    <NavLink exact to="/contact" activeClassName="navActive">
                        <i className="fas fa-id-card"></i>
                        <span>Contact</span>
                    </NavLink>
                    </li>
                </ul>
            </div>

            <div className="socialNetwork">
                <ul>
                    <li>
                        <a href="https://www.linkedin.com/in/mohamed-zied-jlassi-65143bba/" target="_blank" rel="noopenber noreferrer"><i className="fab fa-linkedin"></i></a>
                    </li>
                    <li>
                        <a href="https://github.com/Jlassi-mohamed-Zied" target="_blank" rel="noopenber noreferrer"><i className="fab fa-github"></i></a>
                    </li>
                    <li>
                        <a href="https://twitter.com/jlassimedzied" target="_blank" rel="noopenber noreferrer"><i className="fab fa-twitter"></i></a>
                    </li>
                </ul>
                <div className="signature">
                    <p>Jlassi Mohamed Zied - 2021</p>
                </div>
            </div>


        </div>
    );
};

export default Navigation;