import React from 'react';
import Navigation from '../components/Navigation';
import Text from '../components/Text';

const home = () => {
    return (
        <div className="home">
          <Navigation />
          <div className="homeContent">
            <div className="content">
              {/* <h1>JLASSI MOHAMED ZIED</h1> */}
                <h3><Text
                  strings={[
                    "Bonjour à tous et à toutes, je me présente je m'appelle Zied",
                    "Après une carrière m'ayant fait découvrir plusieurs milieux professionnels et exercer plus de 5 métiers différents. J'ai décidé de me lancer à fond dans l'apprentissage du code, depuis près de 3 mois je me forme à temps plein en autodidacte aux langages Front tels que CSS, Javascript et son Framework React afin de devenir le plus rapidement possible développeur. Plus qu'un avenir professionnel, je me suis découvert une passion, celle de coder."
                  ]}
                  /></h3>
              <div className="pdf">
                <a href="./media/cv.pdf" target="_blank">CV en PDF</a>
              </div>
            </div>
          </div>
        </div>
    );
};

export default home;